import React, {useRef} from 'react'
import Drake from './drake.jpg'
import { motion, useViewportScroll, useTransform } from "framer-motion";

function Example(){
  const { scrollYProgress } = useViewportScroll();
  const scale = useTransform(scrollYProgress, [0, 1], [0.2, 2]);
  const constraintsRef = useRef(null)
  const spring = {
    type: "spring",
    damping: 10,
    stiffness: 100,
    duration: 0.7,
    delay:1
  }

  const anim = (duration, delay) => {
    return {
      type: "spring",
        damping: 10,
          stiffness: 100,
            duration: duration,
              delay: delay
    }
    
  }

  return (
    <div
      className="flex justify-center place-items-center h-screen bg-purple-100">
      <motion.div 
        // whileHover={{scale:1.1}}
        initial={{ opacity: 0, x: '-100vw' }}
        transition={spring} 
        animate={{ opacity: 2, x: 0, }}
        className=" overflow-hidden rounded-md w-3/5 shadow-lg " 
        >
        <motion.div
        ref={constraintsRef}
        
        >
        <motion.img src={Drake} alt="drake" 
          
          drag
          dragConstraints={constraintsRef}
          initial={{ opacity: 0,}}
          animate={{ opacity: 1, }}
          transition={anim(0.2, 0.4)}
          whileTap={{ scale: 2 }}
          whileHover={{scale:1.1}}
          /> 
        </motion.div>
        
        <motion.div className=" text-xs md:text-sm px-6 py-4 "
        
        initial={{ opacity: 0, scale:0 }}
          animate={{ opacity: 1, scale:1 }}
          transition={{
            delay: 1.9,
            duration:0.7
          }}
          
        >
          Aute laborum labore anim consectetur pariatur ad consectetur voluptate enim veniam sit dolore eiusmod. Laborum reprehenderit sint voluptate cillum elit culpa ea officia dolor labore ea minim elit. 
            <div className="my-3  ">
            <motion.span className="mr-2 rounded-full py-2 px-4   bg-gray-300 "
            initial={{ opacity: 0, scale:0 }}
            animate={{ opacity: 1, scale:1 }}
            transition={{
            delay: 2.3,
            duration:0.7
          }}
            >#Music</motion.span>
            <motion.span className="mr-2 rounded-full py-2 px-4   bg-gray-300 "
            initial={{ opacity: 0, scale:0 }}
            animate={{ opacity: 1, scale:1 }}
            transition={{
            delay: 2.6,
            duration:0.7
          }}
            >#Art</motion.span>
            <motion.span className="mr-2 rounded-full py-2 px-4   bg-gray-300 "
            initial={{ opacity: 0, scale:0 }}
            animate={{ opacity: 1, scale:1 }}
            transition={{
            delay: 2.8,
            duration:0.7
          }}
            >#Tour</motion.span>
            </div>
          </motion.div>
          
        </motion.div>
    </div>
  );
};

export default Example


